import React from "react";
import "./App.css";
import Banner from "./components/Banner";
function Contact() {
  return (
    <>
      <Banner
        nameWeb="Lin h vi chng ti"
        description=""
        style={{ backgroundColor: "#30AADD" }}
      />
      <section className="py-5">
        <div className="container px-4 px-lg-5">
          <div className="row gx-4 gx-lg-5 justify-content-center">
            <div className="col-lg-8 col-md-10">
              <h2 className="fw-bolder mb-4 text-center">Lin h</h2>
              <p className="text-center mb-4">
                Nu bn c bt k cu hi no, vui lng lin h vi chng ti
                bng cch in vo biu mu bn di hoc thng qua thng tin
                lin h c cung cp.
              </p>
              {/* Contact form*/}
              <form id="contactForm">
                <div className="form-floating mb-3">
                  <input
                    className="form-control"
                    id="name"
                    type="text"
                    placeholder="Enter your name..."
                    required
                  />
                  <label htmlFor="name">H tn y </label>
                </div>
                <div className="form-floating mb-3">
                  <input
                    className="form-control"
                    id="email"
                    type="email"
                    placeholder="name@example.com"
                    required
                  />
                  <label htmlFor="email">a ch email</label>
                </div>
                <div className="form-floating mb-3">
                  <input
                    className="form-control"
                    id="phone"
                    type="tel"
                    placeholder="(123) 456-7890"
                  />
                  <label htmlFor="phone">S in thoi</label>
                </div>
                <div className="form-floating mb-3">
                  <textarea
                    className="form-control"
                    id="message"
                    placeholder="Enter your message here..."
                    style={{ height: "10rem" }}
                    required
                    defaultValue={""}
                  />
                  <label htmlFor="message">Li Nhn:</label>
                </div>
                <div className="d-grid">
                  <button
                    className="btn btn-outline-success btn-lg"
                    type="submit"
                  >
                    Gi
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
export default Contact;
